    text: `
    <h2>Tipps &amp; Tricks</h2>
    <h3>Suche nach Phrasen</h3>
    <p>Suche nach zusammengehrigen Ausdrcken:"Hhere Tchterschule Zrich"<br /> (Verwenden sie dazu die sog. Doppelten Kodierungszeichen)</p>
    <h3>Einschrnkungen</h3>
    <p>Die Suche kann eingeschrnkt werden auf einzelne Bereiche des Dokuments</p>
    <ul>
      <li>title:Pestalozzi (Suche nur im Titel des Dokuments)</li>
      <li>body:Pestalozzi (Suche nur im Volltext des Dokuments)</li>
    </ul>
    <p>Die Suche kann eingeschrnkt werden auf einzelne Metadaten</p>
    <ul>
      <li>author:"Johann Heinrich Pestalozzi" (Suche nach Autor) </li>
      <li>publicationplace:Bern (Suche nach Publikationsort)</li>
      <li>publicationdate:1831 (Suche nach Publikationsdatum)</li>
      <li>municipality:Aarau (Suche nach Gemeinde)</li>
      <li>canton:Zrich (Suche nach Kanton)</li>
      <li>nation:Frankreich (Suche nach Nation)</li>
      <li>actors:Behrden (Suche nach Akteuren)</li>
      <li>texttype:Lehrmittel (Suche nach Texttypen)</li>
      <li>mediatype:Grafik (Suche nach Medientyp)</li>
      <li>language:Franzsisch (Suche nach Sprache)</li>
      <li>project:Stapfer-Enqute (Suche nach Bestand/Projekt)</li>
      <li>educationssystem:Vorschule (Suche nach Bildungssystematik)</li>
      <li>subject:Geschlecht (Suche nach Bezugsthema)</li>
    </ul>
    <h3>Suchoperatoren</h3>
    <p>Standardmssig werden Suchausdrcke mit <em>oder</em> verknpft, d.h. <br/><em>Kindergarten municipality:Kilchberg</em><br/> sucht nach Dokumenten in denen entweder Kindergarten vorkommt oder die Gemeinde Kilchberg.</p>
    <p>Wird das Zeichen + verwendet, kommt der nachstehende Begriff zwingend vor. Wird das Zeichen - verwendet, kommt der nachstehende Begriff zwingend nicht vor.<br /> Folgende zwei Beispiele verdeutlichen die genaue Anwendung:<br/> +Kindergarten +Frbel (Alle Suchergebnisse
      enthalten zwingend beide Begriffe Kindergarten und Frbel)<br/> +Kindergarten -Frbel (Alle Suchergebnisse enthalten Kindergarten ohne den Begriff Frbel zu enthalten)<p>
      <p> Um eine beliebige Anzahl Zeichen am Ende des Suchbegriffs offen zu lassen, kann das Zeichen * verwendet werden. </br>
      Pestal* (Suchergebnisse sind Pestalozzi, Pestalozzianum, Pestalozzi-Schule, etc.) </p>
      `
